.app {
  margin: 0;
}

.workspace {
  width: 100%;
  background-color: rgb(244, 241, 252);
  padding-top: 15px;
  padding-bottom: 15px;
}

.page {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  margin-left: auto;
  margin-right: auto;

  -webkit-box-shadow: 1px 1px 15px 0px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 1px 1px 15px 0px rgba(34, 60, 80, 0.2);
  box-shadow: 1px 1px 15px 0px rgba(34, 60, 80, 0.2);

  width: 75%;
  height: calc(100vh - 80px);

  background-color: rgb(255, 255, 255);
}

.left-column {
  float: left;
  background: rgb(230, 230, 230);

  width: 30%;
}

.main-column {
  float: right;
  border: 1px solid red;
  width: 70%;
}
